// DEFAULT OVERRIDES

body {
	background: url($baseurl + "/assets/img/background.jpg") no-repeat center center fixed;
	background-size: cover;
	text-size-adjust: none;
	word-wrap: break-word;
	font-family: Times New Roman;
	color: $color_font;
	font-size: 18px;
}

table, th, td {
	border-collapse: collapse;
	border-spacing: 0;
	padding: 0;
}

a {
	color: $color_link_orange;
	text-decoration: none;

	&:hover {
		color: $color_link_orange_hover;
	}
}

h1, h2, h3, h4, h5, h6 {
	font-weight: normal;
	text-decoration: underline;
}
h1 {
	margin: 0.4em;
	font-size: 2.67em;
}
h2 {
	margin: 0.4em;
	font-size: 2.0em;
}
h3 {
	margin: 0.4em;
	font-size: 1.6em;
}
h4 {
	margin: 0.4em;
	font-size: 1.45em;
}
h5 {
	margin: 0.4em;
	font-size: 1.33em;
}
h6 {
	margin: 0.4em;
	font-size: 1.18em;
}


// SITE STRUCTURE

header {
	background-image: url($baseurl + "/assets/img/color_purple.png");
	height: 150px;
	width: 100%;
	background-position: center;

	div {
		background-position: center;
		background-repeat: no-repeat;
	}
	.headerBg {
		background-image: url($baseurl + "/assets/img/header_text.png");
		width: 100%;
		height: 100%;
	}
	.headerFace {
		background-size: contain;
		width: 100px;
		height: 100%;
	}
	.headerFaceLeft {
		background-image: url($baseurl + "/assets/img/header_cicero.png");
		padding-left: 40px;
		float: left;
	}
	.headerFaceRight {
		background-image: url($baseurl + "/assets/img/header_nepos.png");
		padding-right: 20px;
		float: right;
	}
	.headerMobile {
		height: 100%;
		width: 100%;
		text-align: center;
	}
}

nav {
	background-image: url($baseurl + "/assets/img/color_blue.png");
	margin-top: 2px;
	padding: 1px;

	>hr {
		margin: 5px 0px 8px 0px;
	}
	>hr:last-child {
		margin: 6px 0px 0px 0px;
	}
	>ul {
		padding: 0px 5px;
		list-style-type: none;
		margin: auto;
		overflow: hidden;
	}
	li {
		float: left;

		a {
			color: $color_link_grey;
			text-decoration: none;

			&:hover {
				color: $color_link_grey_hover;
			}
		}
	}
}

main {
	background-image: url($baseurl + "/assets/img/color_blue.png");
	text-align: center;
	padding: 30px 15px 50px 15px;
}

footer {
	margin-top: 2px;
	padding: 19px 0px;
	display: grid;
	grid-template-columns: 200px auto 200px;
	align-items: center;
	position: relative;
	text-align: center;
	font-size: 0.9em;
	color: $color_grey;
	background-image: url($baseurl + "/assets/img/color_purple.png");

	>div:nth-child(2) a {
		color: $color_link_blue;

		&:hover {
			color: $color_link_blue_hover;
		}
	}
	.copyright {
		color: #ffffff;
		font-size: 1.12em;
		margin-bottom: 10px;

		a {
			color: #ffffff !important;

			&:hover {
				text-decoration: underline;
			}
		}
	}
	.nav_btm {
		li {
			display: inline-block;
			font-size: 1.12em;

			a {
				color: $color_grey;
				text-align: center;
				padding-left: 5px;
				padding-right: 5px;
				text-decoration: none;

				&:hover {
					color: $color_link_grey_hover;
				}
			}
		}
	}
}


// DIV TABLES

div.table {
	display: table;
	border-collapse: collapse;
	width: 100%;
}
div.tr { 
	display:table-row; 
}
div.td { 
	display:table-cell;
}


