}
.big2 {
	margin: 0.4em;
	font-size: 2.0em;
}
.big3 {
	margin: 0.4em;
	font-size: 1.6em;
}
.big4 {
	margin: 0.4em;
	font-size: 1.45em;
}
.big5 {
	margin: 0.4em;
	font-size: 1.33em;
}
.big6 {
	margin: 0.4em;
