.sectionLinksTranslation {
	font-weight: bold;

	ul {
		padding: 0;
	}
	li {
		display: inline-block;
		margin: 0px 10px 10px 10px;
	}
	a {
		color: $color_link_orange;
		text-decoration: none;

		&:hover {
			color: $color_link_orange_hover;
		}
	}
}

.idReferrer {
	margin: 0px auto 75px auto;
	max-width: 80%;

	ul {
		margin-top: 0px;
		padding: 0px;
	}
	li {
		display: inline-block;
	}
	a {
		color: $color_link_orange;
		padding: 0px 5px;
		text-decoration:none;

		&:hover {
			color: $color_link_orange_hover;
		}
	}
}



.chapter {
	display: grid;

	p {
		margin: unset;
	}
	h2 {
		color: $color_orange;
		grid-column: 1/10;
		margin-bottom: 10px;
	}
	sup {
		font-size: 0.75em;
		color: $color_orange-dark;
	}
	.annotation {
		color: $color_grey;
	}
	.story {
		grid-column: 1/10;
		width: 100%;
		max-width: 1000px;
		margin: auto;
	}

	.chapter-item {
		grid-column: 1/10;
		max-width: 1000px;
		margin: auto;
		display: grid;
		grid-template-columns: 49% 2% 49%;

		p {
			text-align: left;
		}
	}
	.poem-item {
		grid-column: 1/10;
		display: grid;
		grid-template-rows: auto;
		grid-template-columns: 43px 1px 40% 1px auto;
		text-align: center;
		margin-left: -15px;
		margin-right: -15px;

		&:last-child {
			margin-bottom: 10px;
		}
		>div:nth-child(even) {
			background-position: fixed;
			background: url($baseurl + "/assets/img/background.jpg") no-repeat center center fixed;
		}
		p {
			text-align: inherit;

			&:nth-child(1) {
				color: $color_orange-dark;
			}
			&:not(:nth-child(1)) {
				padding: 0px 25px;
			}
		}
	}

	&:last-child {
		.poem-item {
			margin-bottom: -50px;

			p:not(:first-child) {
				padding-bottom: 50px;
			}
		}
	}
}
.footnotes {
	color: $color_grey;
	font-size: 0.89em;
	text-align: left;

	sup {
		color: $color_orange-dark;
	}
}



.translation-sources {
	margin-top: 2px;
	padding: 22px 10px;
	background-image: url($baseurl + "/assets/img/color_blue.png");
	text-align: center;

	a {
		color: $color_link_blue;

		&:hover {
			color: $color_link_blue_hover;
		}
	}
}







.hofItem {
	max-width: 640px;
	display: grid;
	grid-template-rows: 1fr auto 30px;
	text-align: center;
	color: #666666;
	border-radius: 35px;

	div:first-child {
		margin-bottom: 10px;
		font-size: 1.5em;
		font-weight: bold;
		border-bottom: groove 2px;
	}
}


// HELPER CLASSES

.helperVerticalAlignment {
	display: inline-block;
	height: 100%;
	vertical-align: middle;
}


.ShowOnSmallScreen {
	display: none;
}
.ShowOnBigScreen {
	display: block;
}

/*@media only screen and (max-width: 1132px) {
	.trlPoem tr td:nth-child(even) {
		max-width: 0px;
		min-width: 0px;
	}
}*/
@media only screen and (max-width: 1000px) {
	footer {
		grid-template-columns: 1fr auto auto 1fr;

		>div {
			&:nth-child(1), &:nth-child(3) {
				margin-bottom: 15px;
			}
			&:nth-child(1) {
				grid-column: 2/3;
				margin-right: 18px;
			}
			&:nth-child(3) {
				grid-column: 3/4;
				margin-left: 18px;
			}
			&:nth-child(4) {
				grid-column: 2/4;
			}
		}
	}
	.chapter {
		.chapter-item {
			grid-template-columns: 43% 2% 55%;
		}
		.poem-item {
			p {
				&:not(:nth-child(1)) {
					padding: 0px 5px;
				}
			}
		}
	}
	
	.ShowOnSmallScreen {
		display: block;
	}
	.ShowOnBigScreen {
		display: none;
	}
}

@media only screen and (max-width: 400px) {
	footer {
		grid-template-columns: 1fr 1fr;

		>div {
			&:nth-child(1), &:nth-child(3) {
				margin-bottom: 15px;
			}
			&:nth-child(1) {
				grid-column: 1/2;
				margin-right: 0px;
			}
			&:nth-child(3) {
				grid-column: 2/3;
				margin-left: 0px;
			}
			&:nth-child(4) {
				grid-column: 1/3;
			}
		}
	}
	.chapter {
		.poem-item {
			p {
				&:not(:nth-child(1)) {
					padding: 0px 5px;
				}
			}
		}
	}
}

@media only screen and (max-width: 300px) {
	footer {
		grid-template-columns: 1fr;

		>div {
			&:nth-child(1) {
				grid-column: 1/2;
				margin-bottom: 5px;
			}
			&:nth-child(3) {
				grid-column: 1/2;
				margin-bottom: 10px;
			}
			&:nth-child(4) {
				grid-column: 1/2;
			}
		}
	}
	.chapter {
		.poem-item {
			grid-template-columns: unset;

			p {
				&:not(:nth-child(1)) {
					padding: 0px 5px;
				}
			}
		}
	}
}





.alert {
	padding: 20px;
	color: white;
	margin-bottom: 15px;
	font-size: 0.9em;
}
.alertRed {
	background-color: #f44336;
}
.alertYellow {
	background-color: #ff9800;
}
.alertGreen {
	background-color: #4CAF50;
}
.alertBlue {
	background-color: #2196F3;
}

.closebtn {
	margin-left: 15px;
	color: white;
	font-weight: bold;
	float: right;
	font-size: 1.3em;
	line-height: 20px;
	cursor: pointer;
	transition: 0.3s;
}
.closebtn:hover {
	color: black;
} 



.spc {
	float: left;
	color: $color_grey;
	text-align: center;
	padding: 0 5px;
	text-decoration: none;
}


.navRight {
	float: right;
	color: $color_grey;

	li:not(:last-child) {
		margin-right: 5px;
	}
}


.li ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
}
.li li a {
	float: center;
	text-align: center;
}
.li li a:hover {
	color: #0000CC;
}






.background {
	position: fixed;
	z-index: -1;
